<template>
  <div class="cNav" :class="['semi-dark']">
    <div class="wrapper wrapper-padding">
      <!-- <div><span>Keanu Dela Cruz</span></div> -->
      <div class="code-icon">
        <img src="@/assets/icons/code.png" alt="" srcset="">
      </div>

      <div>
        <ul class="menu-list">
          <li @click="selectMenu(item)" class="menu-item t-upper" v-for="(item, key) in menuItems" :key="key">
            {{ item.name }}
          </li>
          <li class="menu-item t-upper">
            <a :href="`${publicPath}`" download="download">Resume</a>
          </li>
        </ul>

        <div class="burger">
          <img @click="showMenu()" class="burger" src="@/assets/icons/burger.png" />
        </div>
      </div>
    </div>

    <Drawer :transfer="!showSideMenu" placement="right" :class-name="'c-drawer'" title="" :closable="true"
      v-model="showSideMenu">
      <ul class="drawer-menu-list">
        <li @click="selectMenu(item)" class="drawer-menu-item t-upper" v-for="(item, key) in menuItems" :key="key">
          {{ item.name }}
        </li>
        <li class="drawer-menu-item t-upper">
          <a :href="`${publicPath}`" download="download">Resume</a>
        </li>
      </ul>
    </Drawer>
  </div>
</template>

<script>
export default {
  name: 'cNav',
  data() {
    return {
      publicPath: '',
      showSideMenu: false,
      menuItems: [
        {
          name: 'Home',
          icon: '',
          id: 'Home',
        },
        {
          name: 'About Me', // intro, experience and projects
          icon: '',
          id: 'AboutMe',
        },
        {
          name: 'Skills',
          icon: '',
          id: 'Skills',
        },
        {
          name: 'Projects',
          icon: '',
          id: 'Projects',
        },
        // {
        //   name: 'Resume',
        //   icon: '',
        //   id: 'Resume',
        // },
      ]
    };
  },
  mounted() {
    this.publicPath = process.env.BASE_URL+'Resume(Keanu Dela Cruz).pdf'
    window.addEventListener('resize', this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    selectMenu(e) {
      if (e.name == 'Resume') {
        this.downloadFile();
        return
      }
      this.$emit('select-menu', e.id)
    },
    showMenu() {
      this.showSideMenu = true;
    },
    handleResize() {
      if (window.innerWidth > 680) {
        this.showSideMenu = false;
      } else {
      }

    },
    async downloadFile() {
      const response = await fetch('https://github.com/iAmKean/portfolio/my_cv.pdf');
      const blob = await response.blob();
      const fileName = 'my_cv.pdf';

      // Create a temporary link element to initiate the download
      const link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      link.download = fileName;
      link.style.display = 'none';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  }
}
</script>

