<style scoped lang="less">
.cSkill {
    min-height: 100vh;
    padding-top: 8rem;
    // margin-bottom: 8rem;

    .title {
        margin-bottom: 1.75rem;
        font-size: 2rem;
        line-height: 2rem;
    }

    .skill-list {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
    }

    .skill-item {
        width: calc(50% - 1rem);
        margin-right: 1rem;
        border: 1px solid #1d293a;
        background-color: #1d293a;
        border-radius: 3px;
        padding: 2rem 1rem 1rem;
        margin-bottom: 1rem;
        cursor: pointer;
        transition: transform .2s;

        &:hover {
            transform: scale(1.05);
        }

        .info {
            display: flex;
            align-items: center;

            &>div:last-child {
                flex: 1;
            }

            img {
                height: 55px;
                margin: auto;
            }

            .skill-icon {
                margin-right: 1rem;
                min-width: 80px;
            }

            .skill-title {
                font-size: 25px;
            }

            .description {
                font-size: 16px;
                margin-top: 1rem;
                width: 250px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .arrow {
            margin-top: 1rem;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            &>div {
                cursor: pointer;
            }
        }
    }
}
</style>

<template>
    <div class="cSkill">
        <div class="wrapper">
            <div class="title t-upper">
                Skills
            </div>
            <div class="skill-list">
                <div class="skill-item" v-for="(item, key) in skills" :key="key">
                    <div class="info">
                        <div class="skill-icon">
                            <img :src="item.icon" alt="" srcset="">
                        </div>
                        <div>
                            <div class="skill-title t-upper">{{ item.title }}</div>
                            <!-- <div class="description">{{ item.description }}</div> -->
                        </div>
                    </div>
                    <div class="arrow">
                        <div>
                            <Icon type="md-arrow-round-forward" size="25" />
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
export default {
    name: 'cSkill',
    data() {
        return {
            skills: [
                {
                    title: 'Javascript',
                    icon: require('./../assets/icons/js.png'),
                    description: 'JavaScript is a versatile programming language that plays a vital role in my experience as a Front-End Developer. It empowers me to create dynamic and interactive elements on websites and web applications. With JavaScript, I can enhance user experiences by adding functionality, interactivity, and responsiveness to the user interface. I utilize JavaScript frameworks like Vue JS to develop and improve web applications, build mobile apps, templates, and reusable components. Additionally, JavaScript enables me to add interactivity, animations, and dynamic content to web pages, enhancing the overall user experience and contributing to the success of various web development projects.',
                    link: ''
                },
                {
                    title: 'HTML',
                    icon: require('./../assets/icons/html.png'),
                    description: 'HTML (HyperText Markup Language) is the fundamental markup language that has been integral to my experience as a Front-End Developer. It provides the essential elements to structure the content of web pages, such as headings, paragraphs, lists, tables, forms, and more. By utilizing HTML tags and attributes, I can organize text, images, and other media in a structured and semantically meaningful manner, forming the backbone of web pages.',
                    link: ''
                },
                {
                    title: 'CSS',
                    icon: require('./../assets/icons/css.png'),
                    description: 'CSS (Cascading Style Sheets) is a crucial style language that complements HTML by controlling the presentation and layout of web pages. With CSS, I can define the appearance of HTML elements, including fonts, colors, margins, padding, and positioning. This enables me to customize the visual aspects of a website, creating visually appealing, consistent, and user-friendly designs. CSS also allows me to implement responsive layouts, ensuring a seamless user experience across different devices and screen sizes.',
                    link: ''
                },
                {
                    title: 'Vue.Js',
                    icon: require('./../assets/icons/vuejs.png'),
                    description: 'Vue.js is a popular JavaScript framework that has been a significant part of my experience as a Front-End Developer. It allows me to build dynamic and interactive user interfaces with ease. Vue.js follows a component-based architecture, enabling me to create reusable and modular components, leading to more efficient development and easier maintenance. With Vue.js, I can manage the state of applications seamlessly, making it simpler to handle data and user interactions. Its versatility and simplicity have made it a valuable tool for creating modern web applications.',
                    link: ''
                },
                {
                    title: 'Electron.Js',
                    icon: require('./../assets/icons/electronjs.png'),
                    description: 'Electron.js is a framework that allows me to build cross-platform desktop applications using web technologies like HTML, CSS, and JavaScript. It has been a game-changer for my development experience, as I can leverage my web development skills to create desktop applications that run on Windows, macOS, and Linux systems. Electron.js provides access to native APIs, enabling me to integrate desktop-specific features into my applications, making them feel like native apps while utilizing web technologies for the user interface.',
                    link: ''
                },
                {
                    title: 'Nuxt.Js',
                    icon: require('./../assets/icons/nuxtjs.png'),
                    description: 'Nuxt.js is a powerful framework based on Vue.js that I have used to create server-side-rendered (SSR) and statically generated (SSG) applications. It simplifies the process of building scalable and high-performance Vue.js applications by providing out-of-the-box features like routing, code-splitting, and server-side rendering. Nuxt.js helps improve SEO and performance, as well as facilitates a smoother user experience. Its ability to generate static files makes it ideal for deploying fast-loading and SEO-friendly websites.',
                    link: ''
                },
                {
                    title: 'SASS',
                    icon: require('./../assets/icons/sass.png'),
                    description: 'Sass is a CSS preprocessor that has been an integral part of my Front-End Development workflow. It extends the capabilities of CSS by adding variables, nesting, mixins, and other useful features, allowing me to write cleaner and more maintainable stylesheets. With Sass, I can organize CSS code more efficiently, making it easier to manage complex projects and apply consistent styles across different components and pages. Sass has significantly enhanced my productivity by reducing repetitive tasks and encouraging code reusability.',
                    link: ''
                },
                {
                    title: 'GitHub',
                    icon: require('./../assets/icons/git.png'),
                    description: "Git is a version control system that has been an essential tool in my development process. It allows me to track changes in code, collaborate with other developers, and manage project versions effectively. With Git, I can create branches to work on specific features or bug fixes without affecting the main codebase. This enables me to experiment and test changes before merging them into the main project. Git's ability to handle code merging and conflict resolution makes it an invaluable tool for team collaboration and maintaining a clean and stable codebase.",
                    link: ''
                }
            ]
        };
    },
}
</script>


<style scoped lang="less">
.cSkill {
    @media (min-width: 2420px) {}

    @media (max-width: 1920px) {}

    @media (max-width: 1560px) {}

    @media (max-width: 1440px) {}

    @media (max-width: 1200px) {}

    @media (max-width: 1024px) {}

    @media (max-width: 992px) {
        .title {
            font-size: 1.75rem;
        }
    }

    @media (max-width: 880px) {}

    @media (max-width: 768px) {}

    @media (max-width: 680px) {
        .skill-list {
            flex-wrap: wrap;
        }

        .skill-item {
            width: 100%;

            .info .skill-title {
                font-size: 1.25rem;
            }
        }
    }

    @media (max-width: 600px) {}

    @media (max-width: 576px) {}

    @media (max-width: 495px) {}

    @media (max-width: 480px) {}

    @media (max-width: 385px) {
        .skill-item {
            width: 100%;

            .info .skill-title {
                font-size: 1.05rem;
            }
        }
    }

    @media (max-width: 320px) {
        .skill-item {
            position: relative;
            width: 100%;

            .info {
                display: block;

                img {
                    margin: unset;
                    margin-bottom: 1rem;
                }


            }

            .arrow {
                margin-top: 0;
                position: absolute;
                bottom: 1rem;
                right: 1rem;
            }

            .info .skill-title {
                font-size: 1rem;
            }
        }
    }
}
</style>