<style scoped lang="less">
.cProjects {
    // margin-bottom: 8rem;
    min-height: 100vh;
    padding-top: 8rem;

    .title {
        margin-bottom: 4.75rem;
        font-size: 2rem;
        line-height: 2rem;
    }


    .content-wrapper {
        display: flex;

        .left {
            margin-right: 1rem;
            padding-top: 4rem;
        }

        .right {
            flex: 1;
            width: 350px;

            img {
                object-fit: cover;
                height: 500px;
                margin: 0 auto;
            }
        }

        .item-image {
            height: 100%;
        }

        .item-title {
            margin-bottom: 1.75rem;
            font-size: 1.25rem;
            line-height: 1.25rem;
            text-transform: uppercase;
        }

        .item-description {
            text-align: left;
            text-indent: 2rem;
            margin-bottom: 1.75rem;
        }

        .item-link {
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 40px;
            border-radius: 6px;
            cursor: pointer;
            background-color: #3FB883;
            color: #fff;
            border: unset;
            width: 120px;
            transition: width 0.5s;
            border-radius: 30px;
            text-align: center;
        }

        .item-tools {
            margin-top: 2rem;
            display: flex;
            flex-wrap: wrap;

            img {
                height: 40px;
                width: auto;
                margin-right: 10px;
            }
        }
    }

}
</style>

<template>
    <div class="cProjects">
        <div class="wrapper">
            <div class="title t-upper">
                Projects
            </div>

            <div>
                <Carousel v-model="activeCarousel" loop :autoplay="true" :autoplay-speed="3000">
                    <CarouselItem v-for="(item, key) in projectList" :key="key">
                        <div class="content-wrapper">
                            <div class="left">
                                <div class="item-title">{{ item.title }}</div>
                                <div class="item-description">{{ item.description }}</div>
                                <div class="item-link">
                                    <div @click="openLiveDemo(item.link)">Live Demo</div>
                                    <Icon type="ios-open-outline" style="margin-left: 5px;" color="#fff" />
                                </div>

                                <div class="item-tools">
                                    <div v-for="(toolItem, toolKey) in item.tools" :key="toolKey">
                                        <img :src="parseString(toolItem)" alt="" srcset="">
                                        <!-- <span>{{ toolItem }}</span> -->
                                    </div>
                                </div>
                            </div>
                            <div class="right">
                                <div class="item-image">
                                    <!-- <img src="@/assets/icons/code.png" alt="" srcset=""> -->
                                    <img :src="`${publicPath}${item.image}`" alt="" srcset="">
                                </div>

                            </div>
                        </div>
                    </CarouselItem>
                </Carousel>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    name: 'cProjects',
    data() {
        return {
            publicPath: '',
            activeCarousel: 0,
            projectList: [],
        }
    },
    async mounted() {
        this.publicPath = process.env.NODE_ENV !== "production" ? 'http://localhost:8080/img/' : process.env.BASE_URL + '/img/';
        fetch("/projects.json")
            .then(response => response.json())
            .then(data => {
                this.projectList = data.projects;
            })
            .catch(error => {
                console.error("Error fetching data:", error);
            });
    },
    methods: {
        parseString(img) {
            if (!img) return '';
            return require(`./../assets/icons/${img}`);
        },
        openLiveDemo(link) {
            if (!link) return;
            window.open(link, "_blank");
        },
    },
}
</script>


<style scoped lang="less">
.cProjects {
    @media (min-width: 1921px) {}

    @media (min-width: 2220px) {}

    @media (min-width: 2420px) {}

    @media (max-width: 1920px) {}

    @media (max-width: 1560px) {}

    @media (max-width: 1440px) {}

    @media (max-width: 1200px) {}

    @media (max-width: 1024px) {}

    @media (max-width: 992px) {
        .title {
            font-size: 1.75rem;
        }

        .content-wrapper {
            display: block;
            margin-bottom: 4rem;

            .left {
                margin-right: 0;
                // flex: 1;
            }

            .right {
                // display: none;
                margin-top: 2rem;
                width: 100%;
            }
        }

    }

    @media (max-width: 880px) {}

    @media (max-width: 768px) {}

    @media (max-width: 680px) {}

    @media (max-width: 600px) {}

    @media (max-width: 576px) {}

    @media (max-width: 495px) {}

    @media (max-width: 480px) {}

    @media (max-width: 385px) {
        min-height: 65vh;

        .content-wrapper {
            .item-title {
                font-size: 1.05rem;
            }

            .item-description {
                text-indent: 1rem;
            }
        }
    }

    @media (max-width: 320px) {
        .content-wrapper {
            .item-title {
                font-size: 1rem;
            }
        }
    }
}
</style>